@-webkit-keyframes animate {
    0% {
        -webkit-transform: scale(0) translateY(0) rotate(0);
                transform: scale(0) translateY(0) rotate(0);
        opacity: 1;
       /* transform: translateY(0); */
    }

    100% {
        -webkit-transform: scale(1.5) translateY(100px) rotate(360deg);
                transform: scale(1.5) translateY(100px) rotate(360deg);
        opacity: 0;
        /* transform: translateY(-80px); */
    }
}


@keyframes animate {
    0% {
        -webkit-transform: scale(0) translateY(0) rotate(0);
                transform: scale(0) translateY(0) rotate(0);
        opacity: 1;
       /* transform: translateY(0); */
    }

    100% {
        -webkit-transform: scale(1.5) translateY(100px) rotate(360deg);
                transform: scale(1.5) translateY(100px) rotate(360deg);
        opacity: 0;
        /* transform: translateY(-80px); */
    }
}
