.cardP {
    margin: 45px auto 20px;
    padding-bottom: 45px;
    border-radius: 16px;
    width: 85%;
    height: 1%;
    background-color: #272838;
    overflow: hidden;
    box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
}

.cardBodyP {
    padding: 1rem;
}

.projectCard{
    margin: 45px 25px 0;
    border-radius: 16px;
    transition: 0.5s ease;
    box-shadow: 0px 3px 18px 3px rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
}

.column {
    float: left;
    width: 32%;
    padding: 0 10px;
}

.projectCard#virtualDesktop{
    background-color: #F47174;
   
}

.projectCard#foodForThought{
    background-color: #6FD397;
}

.projectCard#bao{
    background-color: #7289DA;
}

.projectCard#collaj{
    background-color: #296C97;
}

.projectCard#feedTheCat{
    background-color: #424242;
}

.cardHeaderP {
    font-size: 25px;
    color: white;
    position: relative;

}

.projectCardHeader {
    margin: 10px;
    font-size: 25px;
    color: white;
    text-align: center;
    font-family: Ubuntu-M;
}

.projectCardDesc {
    margin: 10px;
