    overflow: auto;
    text-align: center;
}

.alignBullet {
    list-style-position: inside;
}

ul li { 
    padding: 3px 0px;
}

.externalLinks {
    margin: 10px;
    position: absolute;
    bottom: 0;
    right: 0;
}

.icon-styleP {
    color: white;
}

.icon-styleP:hover {
    color: #F52F57;
    transition: 0.3s;
}

.date {
    margin: 20px;
    font-size: 1.2rem;
    color: white;
    bottom: 0;
    left: 0;
}

.iconP {
    margin-bottom: 15px;
    position: absolute;
    left: 47%;
    bottom: 0px;
    color: white;
    transition: 0.5s ease;
}

.projectCard:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 5px 5px 18px white;
}

.projectCard#virtualDesktop:hover{
    background: linear-gradient(#F47174, #F69D3C);
}
.projectCard#foodForThought:hover{
    background: linear-gradient(#6FD397, #00bfff);
}
.projectCard#bao:hover{
    background: linear-gradient(#7289DA, #e66465);
}
.projectCard#collaj:hover{
    background: linear-gradient(#296C97, #333);
}
.projectCard#feedTheCat:hover{
    background: linear-gradient(#424242, #EB9199);
}

.projectCard:hover .iconP{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}

.cardDivider {
    border: 1px dashed white;
}

